export default ".topic-container {\n  background-color: #daf2e5;\n  margin-bottom: 10px;\n  border: 1px solid #f29e03;\n  border-radius: 10px;\n  overflow: hidden;\n}\n\n.topic-image-container {\n  padding: 10px;\n  background-color: rgba(255, 255, 255, 0.7);\n}\n\n.topic-image-container div {\n  border-radius: 10px;\n}\n\n.topic-image {\n  width: 100%;\n  height: 250px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  background-position: center;\n  margin-right: 10px;\n}\n\n.topic {\n  width: calc(100% - 30px);\n  padding: 10px 15px;\n  background-color: rgba(255, 255, 255, 0.7);\n}\n\n.topic .title {\n  font-size: 18px !important;\n  font-weight: 500;\n  line-height: 1.3;\n  margin-bottom: 10px;\n}\n\n.topic .tags {\n  line-height: 1;\n  margin-bottom: 5px;\n}\n\n.topic .tags .tag-item {\n  display: inline-block;\n  background-color: #0f9d5830;\n  border: 1px solid #0f9d5830;\n  margin-right: 5px;\n  border-radius: 5px;\n  padding: 4px;\n  min-width: 40px;\n  text-align: center;\n}\n\n.topic .tags a {\n  text-decoration: none;\n}\n\n.topic .tags a:hover {\n  color: #f29e03 !important;\n}\n\n.topic .description {\n  margin-top: 5px;\n  margin-bottom: 0px;\n  text-align: justify;\n}\n\n.topic .topic-footer {\n  margin-top: 10px;\n  text-align: right;\n  display: flex;\n}\n\n.topic .topic-footer .date {\n  margin-right: auto;\n  font-size: small;\n  color: #00717b !important;\n}\n\n.topic .topic-footer .topic-link a {\n  color: #00717b !important;\n  margin-right: 10px;\n}\n\n.topic .topic-footer .topic-link a:hover {\n  color: #f29e03 !important;\n}";