export default ".topic-container {\n  background-color: #daf2e5;\n  min-height: 170px;\n  margin-bottom: 18px;\n  border: 1px solid #f29e03;\n  border-radius: 10px;\n  overflow: hidden;\n  display: flex;\n}\n\n.overlay {\n  display: flex;\n  background-color: rgba(255, 255, 255, 0.7);\n}\n\n.topic-image-container {\n  padding: 10px;\n  margin-right: 10px;\n}\n\n.topic-image {\n  border-radius: 10px;\n  width: 150px;\n  height: 150px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  background-position: center;\n  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;\n  background-color: white;\n}\n\n.technologies {\n  line-height: 1;\n  margin-bottom: 5px;\n}\n\n.technologies .tag-item {\n  display: inline-block;\n  background-color: #0f9d5830;\n  border: 1px solid #0f9d5830;\n  margin-right: 5px;\n  border-radius: 5px;\n  padding: 4px;\n  min-width: 40px;\n  text-align: center;\n}\n\n.technologies a {\n  text-decoration: none;\n}\n\n.technologies a:hover {\n  color: #f29e03 !important;\n}\n\n.topic {\n  display: flex;\n  flex-direction: column;\n  height: 100%;\n  padding-right: 20px;\n  padding-top: 10px;\n  padding-bottom: 10px;\n}\n\n.topic .title {\n  font-size: 20px !important;\n  font-weight: 500;\n}\n\n.topic .description {\n  margin-top: 10px;\n  margin-bottom: 10px;\n  overflow: hidden;\n  text-align: justify;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-line-clamp: 3;\n  -webkit-box-orient: vertical;\n  line-height: normal;\n  min-height: 3rem;\n}\n\n.topic .topic-footer {\n  display: flex;\n}\n\n.topic .topic-footer .topic-link a {\n  color: #00717b !important;\n  margin-right: 10px;\n}\n\n.topic .topic-footer span {\n  margin: auto;\n}\n\n.topic-container-invisible {\n  visibility: hidden;\n}\n\n.topic-container-hiden {\n  display: none !important;\n}\n\n.update-next-time {\n  display: flex;\n  flex-direction: row;\n  width: 100%;\n  justify-content: center;\n}\n\n.update-next-time div {\n  margin: auto;\n}\n\n@media scre